//   Telegram Stars
export const isStarsAvailable = () => {
  //   Telegram WebApp   showInvoice
  const webApp = window.Telegram?.WebApp;
  if (!webApp) return false;
  
  //    -   showInvoice
  const hasInvoiceSupport = typeof webApp.showInvoice === 'function';
  
  //    
  const isMobileDevice = /Android|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
  const isTouchDevice = 'ontouchstart' in window || navigator.maxTouchPoints > 0;
  
  console.log('Telegram WebApp platform:', webApp.platform);
  console.log('Has showInvoice:', hasInvoiceSupport);
  console.log('Is mobile device (UA):', isMobileDevice);
  console.log('Is touch device:', isTouchDevice);
  console.log('WebApp version:', webApp.version);
  
  //  true    showInvoice
  return hasInvoiceSupport;
};

//    
export const createInvoice = async (planType) => {
  try {
    const user = getCurrentUser();
    if (!user) {
      throw new Error('  ');
    }

    const plan = SUBSCRIPTION_PLANS[planType];
    if (!plan) {
      throw new Error('  ');
    }

    //   payload
    const payload = {
      userId: user.id,
      planType: planType,
      planId: plan.id,
      timestamp: Date.now(),
    };

    //    Telegram WebApp API
    const invoice = {
      title: plan.title,
      description: plan.description,
      payload: JSON.stringify(payload),
      provider_token: '', //   Telegram Stars
      currency: 'XTR', // Telegram Stars currency
      prices: [{
        label: plan.title,
        amount: plan.amount, // amount in stars
      }],
      start_parameter: `premium_${planType.toLowerCase()}`,
      photo_url: 'https://i-speech-helper-uce4.vercel.app/assets/telegram-star.png',
      photo_size: 512,
      photo_width: 512,
      photo_height: 512,
      need_name: false,
      need_phone_number: false,
      need_email: false,
      need_shipping_address: false,
      send_phone_number_to_provider: false,
      send_email_to_provider: false,
      is_flexible: false,
    };

    console.log(' :', invoice);
    return invoice;
  } catch (error) {
    console.error('  :', error);
    throw error;
  }
};

//    Telegram Stars
export const purchaseWithStars = async (planType) => {
  return new Promise(async (resolve, reject) => {
    try {
      console.log('   :', planType);
      
      const webApp = window.Telegram?.WebApp;
      if (!webApp) {
        throw new Error('Telegram WebApp ');
      }

      console.log('WebApp ,  ...');
      const invoice = await createInvoice(planType);
      console.log(' ,   ...');
      
      //   showInvoice -    
      if (typeof webApp.showInvoice === 'function') {
        console.log('   showInvoice');
        try {
          webApp.showInvoice(invoice, (status) => {
            console.log(' :', status);
            if (status === 'paid') {
              resolve({
                success: true,
                planType: planType,
                amount: SUBSCRIPTION_PLANS[planType].amount,
              });
            } else if (status === 'cancelled') {
              resolve({
                success: false,
                cancelled: true,
                error: '  ',
              });
            } else if (status === 'failed') {
              resolve({
                success: false,
                error: '  ',
              });
            } else {
              resolve({
                success: false,
                error: `  : ${status}`,
              });
            }
          });
        } catch (invoiceError) {
          console.error('   showInvoice:', invoiceError);
          throw invoiceError;
        }
      } else {
        //   -    
        console.log('showInvoice ,   ');
        
        const user = getCurrentUser();
        if (!user) {
          throw new Error('  ');
        }

        try {
          //      API
          const apiUrl = 'http://localhost:5000/api/create-invoice';
            
          const response = await fetch(apiUrl, {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({
              userId: user.id,
              planType: planType,
              userInfo: {
                firstName: user.firstName,
                lastName: user.lastName,
                username: user.username
              }
            }),
          });

          if (response.ok) {
            const result = await response.json();
            
            //     
            const message = ` ! 

     @iSpeechHelper_bot   ${SUBSCRIPTION_PLANS[planType].title}  ${SUBSCRIPTION_PLANS[planType].amount} .

     .`;
            
            if (typeof webApp.showAlert === 'function') {
              webApp.showAlert(message, () => {
                //    
                if (typeof webApp.openTelegramLink === 'function') {
                  webApp.openTelegramLink('https://t.me/iSpeechHelper_bot');
                }
                resolve({
                  success: false,
                  cancelled: true,
                  error: '      ',
                });
              });
            } else {
              alert(message);
              resolve({
                success: false,
                cancelled: true,
                error: '      ',
              });
            }
          } else {
            throw new Error('    ');
          }
        } catch (serverError) {
          console.error('     :', serverError);
          
          // Fallback -   
          const message = ` ${SUBSCRIPTION_PLANS[planType].title}  ${SUBSCRIPTION_PLANS[planType].amount}        Telegram.\n\n     Telegram   .`;
          
          if (typeof webApp.showAlert === 'function') {
            try {
              webApp.showAlert(message, () => {
                resolve({
                  success: false,
                  cancelled: true,
                  error: '     Telegram',
                });
              });
            } catch (alertError) {
              console.error('   showAlert:', alertError);
              alert(message);
              resolve({
                success: false,
                cancelled: true,
                error: '     Telegram',
              });
            }
          } else {
            alert(message);
            resolve({
              success: false,
              cancelled: true,
              error: '     Telegram',
            });
          }
        }
      }

    } catch (error) {
      console.error('  :', error);
      reject(error);
    }
  });
};

