import { getUserSettings } from './storage';

//   
const isVibrationSupported = () => {
  const isMobile = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
  return 'vibrate' in navigator && isMobile;
};

// ,    Telegram WebApp (   )
const isTelegramWebApp = () => {
  return window.Telegram && window.Telegram.WebApp;
};

//     
const patterns = {
  click: 50,
  success: [50, 50, 50],
  error: [100, 50, 100],
};

//   
export const vibrate = (type) => {
  try {
    //   
    const settings = getUserSettings();
    if (settings && settings.vibrationEnabled === false) {
      return; //     
    }

    if (!isVibrationSupported()) {
      return;
    }

    //  Telegram WebApp  Telegram API  
    if (isTelegramWebApp()) {
      try {
        if (window.Telegram.WebApp.HapticFeedback) {
          //  Telegram Haptic Feedback  navigator.vibrate
          switch (type) {
            case 'click':
              window.Telegram.WebApp.HapticFeedback.impactOccurred('light');
              break;
            case 'success':
              window.Telegram.WebApp.HapticFeedback.notificationOccurred('success');
              break;
            case 'error':
              window.Telegram.WebApp.HapticFeedback.notificationOccurred('error');
              break;
            default:
              window.Telegram.WebApp.HapticFeedback.impactOccurred('light');
          }
          return;
        }
      } catch (e) {
        console.error('Telegram HapticFeedback :', e);
      }
    }

    // Fallback   
    const pattern = patterns[type];
    if (pattern) {
      navigator.vibrate(pattern);
    }
  } catch (error) {
    console.error('  :', error);
  }
};

//    
export const stopVibration = () => {
  try {
    if (isVibrationSupported()) {
      navigator.vibrate(0);
    }
  } catch (error) {
    console.error('   :', error);
  }
}; 