import React, { useState, useEffect } from 'react';
import { 
  Box, 
  Typography, 
  Button, 
  Card, 
  CardContent, 
  Chip,
  Alert,
  Stack
} from '@mui/material';
import { CheckCircle, Error } from '@mui/icons-material';

const AnalyticsTest = () => {
  const [analyticsStatus, setAnalyticsStatus] = useState({
    sdkLoaded: false,
    initialized: false,
    telegramWebApp: false,
    errors: []
  });

  useEffect(() => {
    checkAnalyticsStatus();
  }, []);

  const checkAnalyticsStatus = () => {
    const status = {
      sdkLoaded: false,
      initialized: false,
      telegramWebApp: false,
      errors: []
    };

    //   SDK
    if (typeof window.telegramAnalytics !== 'undefined' || typeof telegramAnalytics !== 'undefined') {
      status.sdkLoaded = true;
    } else {
      status.errors.push('SDK  ');
    }

    //  Telegram WebApp
    if (window.Telegram && window.Telegram.WebApp) {
      status.telegramWebApp = true;
    } else {
      status.errors.push('Telegram WebApp ');
    }

    setAnalyticsStatus(status);
  };

  const runAnalyticsTest = () => {
    console.log('   ...');
    
    //   
    if (window.checkTelegramAnalytics) {
      window.checkTelegramAnalytics();
    }
    
    //    Telegram Web
    if (window.testTelegramWeb) {
      window.testTelegramWeb();
    }
    
    //   
    console.log('   Network  DevTools     tganalytics.xyz');
    
    checkAnalyticsStatus();
  };

  const testInTelegramWeb = () => {
    console.log('    Telegram Web');
    
    //   Telegram
    const isTelegramContext = window.Telegram && window.Telegram.WebApp;
    console.log('  Telegram:', isTelegramContext ? '' : '');
    
    if (isTelegramContext) {
      console.log('    Telegram Web');
      console.log(' URL:', window.location.href);
      console.log(' :', window.Telegram.WebApp.platform);
    } else {
      console.log('     Telegram');
      console.log('      Telegram Web');
    }
    
    if (window.testTelegramWeb) {
      window.testTelegramWeb();
    }
  };

  const StatusChip = ({ condition, label }) => (
    <Chip
      icon={condition ? <CheckCircle /> : <Error />}
      label={label}
      color={condition ? 'success' : 'error'}
      variant="outlined"
    />
  );

  return (
    <Box sx={{ p: 3, maxWidth: 600, mx: 'auto' }}>
      <Typography variant="h4" gutterBottom align="center">
          Telegram Analytics
      </Typography>

      <Card sx={{ mb: 3 }}>
        <CardContent>
          <Typography variant="h6" gutterBottom>
             
          </Typography>
          
          <Stack spacing={1} sx={{ mb: 2 }}>
            <StatusChip 
              condition={analyticsStatus.sdkLoaded} 
              label="Telegram Analytics SDK" 
            />
            <StatusChip 
              condition={analyticsStatus.telegramWebApp} 
              label="Telegram WebApp" 
            />
          </Stack>

          {analyticsStatus.errors.length > 0 && (
            <Alert severity="warning" sx={{ mb: 2 }}>
              <Typography variant="subtitle2"> :</Typography>
              <ul>
                {analyticsStatus.errors.map((error, index) => (
                  <li key={index}>{error}</li>
                ))}
              </ul>
            </Alert>
          )}

          <Button 
            variant="contained" 
            onClick={runAnalyticsTest}
            fullWidth
            sx={{ mt: 2 }}
          >
              
          </Button>

          <Button 
            variant="outlined" 
            onClick={testInTelegramWeb}
            fullWidth
            sx={{ mt: 1 }}
            color="primary"
          >
              Telegram Web
          </Button>
        </CardContent>
      </Card>

      <Card>
        <CardContent>
          <Typography variant="h6" gutterBottom>
               
          </Typography>
          
          <Alert severity="info" sx={{ mb: 2 }}>
            <Typography variant="body2">
              <strong>   :</strong>
            </Typography>
          </Alert>

          <Box component="ol" sx={{ pl: 2 }}>
            <Typography component="li" variant="body2" sx={{ mb: 1 }}>
               <strong>DevTools</strong> (F12)   <strong>Console</strong>
            </Typography>
            <Typography component="li" variant="body2" sx={{ mb: 1 }}>
                     
            </Typography>
            <Typography component="li" variant="body2" sx={{ mb: 1 }}>
                 <strong>Network</strong>
            </Typography>
            <Typography component="li" variant="body2" sx={{ mb: 1 }}>
                 <code>tganalytics.xyz</code>
            </Typography>
            <Typography component="li" variant="body2" sx={{ mb: 1 }}>
               : <code>checkTelegramAnalytics()</code>
            </Typography>
            <Typography component="li" variant="body2" sx={{ mb: 1, color: 'primary.main', fontWeight: 'bold' }}>
                Telegram Web: <code>testTelegramWeb()</code>
            </Typography>
          </Box>

          <Alert severity="success" sx={{ mt: 2 }}>
            <Typography variant="body2">
              <strong> Telegram Web:</strong>       web.telegram.org    
            </Typography>
          </Alert>
        </CardContent>
      </Card>
    </Box>
  );
};

export default AnalyticsTest; 