import { getCurrentUser } from './telegram';

const API_URL = process.env.REACT_APP_API_URL || 'https://ispeech-backend.onrender.com/api';

//     
let subscriptionCache = null;
let cacheTimestamp = null;
let pendingRequest = null;
const CACHE_DURATION = 30000; // 30 

const SUBSCRIPTION_TYPES = {
  MONTHLY: {
    id: 'monthly',
    stars: 300,
    duration: 30, // 
  },
  QUARTERLY: {
    id: 'quarterly',
    stars: 720,
    duration: 90, // 
  },
  YEARLY: {
    id: 'yearly',
    stars: 2160,
    duration: 365, // 
  },
};

//  Telegram Stars
export const initTelegramStars = () => {
  if (window.Telegram?.WebApp?.Stars) {
    return window.Telegram.WebApp.Stars;
  }
  return null;
};

//   
const isCacheValid = () => {
  return subscriptionCache && cacheTimestamp && (Date.now() - cacheTimestamp < CACHE_DURATION);
};

//     retry
const fetchWithRetry = async (url, options, maxRetries = 2) => {
  for (let attempt = 0; attempt <= maxRetries; attempt++) {
    try {
      const controller = new AbortController();
      const timeoutId = setTimeout(() => controller.abort(), 8000); // 8 

      const response = await fetch(url, {
        ...options,
        signal: controller.signal,
      });

      clearTimeout(timeoutId);
      return response;
    } catch (error) {
      if (attempt === maxRetries) {
        throw error;
      }
      
      //    
      await new Promise(resolve => setTimeout(resolve, 1000 * (attempt + 1)));
    }
  }
};

//     
export const checkSubscriptionStatus = async () => {
  try {
    const user = getCurrentUser();
    if (!user) {
      console.log('     ');
      return { 
        isActive: false,
        type: null,
        expiresAt: null,
      };
    }

    //  
    if (isCacheValid()) {
      return subscriptionCache;
    }

    //     ,  
    if (pendingRequest) {
      return await pendingRequest;
    }

    //   
    const url = `${API_URL}/subscriptions/status/${user.id}`;

    pendingRequest = fetchWithRetry(url, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
    }).then(async (response) => {
      if (!response.ok) {
        if (response.status === 404) {
          return {
            isActive: false,
            type: null,
            expiresAt: null,
          };
        }
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }

      const data = await response.json();
      
      //   
      subscriptionCache = data;
      cacheTimestamp = Date.now();
      
      return data;
    }).catch((error) => {
      if (error.name === 'AbortError') {
        throw new Error('   ');
      }
      throw error;
    }).finally(() => {
      //   
      pendingRequest = null;
    });

    return await pendingRequest;

  } catch (error) {
    //       
    if (!error.message.includes(' ') && error.name !== 'AbortError') {
      console.error('   :', error);
    }
    
    //     
    pendingRequest = null;
    
    //    
    if (error.name === 'TypeError' && error.message.includes('fetch')) {
      return { error: '  .    .' };
    }
    
    if (error.message.includes('CORS')) {
      return { error: '  .   .' };
    }
    
    //   ,        
    if (error.message.includes('   ') || error.name === 'AbortError') {
      //        
      return {
        isActive: false,
        type: null,
        expiresAt: null,
      };
    }
    
    return { error: ` : ${error.message}` };
  }
};

//   (,   )
export const clearSubscriptionCache = () => {
  subscriptionCache = null;
  cacheTimestamp = null;
  pendingRequest = null;
};

//  
export const purchaseSubscription = async (type) => {
  try {
    const stars = initTelegramStars();
    if (!stars) {
      throw new Error('Telegram Stars  ');
    }

    const subscription = SUBSCRIPTION_TYPES[type];
    if (!subscription) {
      throw new Error('  ');
    }

    const user = getCurrentUser();
    if (!user) {
      throw new Error('  ');
    }

    //   
    const result = await stars.purchase({
      productId: subscription.id,
      amount: subscription.stars,
    });

    if (result.status === 'success') {
      //      
      const response = await fetch(`${API_URL}/subscriptions/create`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          userId: user.id,
          type: subscription.id,
          stars: subscription.stars,
          transactionId: result.transactionId,
        }),
      });

      if (!response.ok) {
        throw new Error('Failed to create subscription');
      }

      const data = await response.json();
      
      //     
      clearSubscriptionCache();
      
      return {
        success: true,
        subscription: data.subscription,
      };
    }

    return {
      success: false,
      error: result.error || '   ',
    };
  } catch (error) {
    console.error('   :', error);
    return {
      success: false,
      error: error.message,
    };
  }
};

//    
export const getSubscriptionInfo = (type) => {
  return SUBSCRIPTION_TYPES[type] || null;
};

const subscriptionService = {
  initTelegramStars,
  checkSubscriptionStatus,
  purchaseSubscription,
  getSubscriptionInfo,
  clearSubscriptionCache,
};

export default subscriptionService; 